 \"@assets/sass/mixins/_vendor.mixins.scss\" as vnd;\n\n.interestingFactSlide {\n  @include mut.sized(387px, 416px);\n  @include mut.full-rounded(33px);\n\n  z-index: 0;\n  cursor: pointer;\n  position: relative;\n  @include vnd.vendored(transition, '.5s ease');\n\n  background-color: c.$pure-white-color;\n  box-shadow: 0 2.62461px 2.62461px rgba(0, 0, 0, .25);\n\n  .slideImage {\n    @include mut.sized(100%, 250px);\n    @include mut.rounded(33px, 33px);\n  }\n\n  .slideText {\n    @include mut.sized(100%, 167px);\n    @include mut.rounded($bottom-right: 33px, $bottom-left: 33px);\n\n    .heading {\n      @include mut.rem-padded(11px, 20px, 10px, 20px);\n      color: c.$accented-red-color;\n\n      @include mut.with-font($font-weight: 500, $font-size: 17px);\n      line-height: f.pxToEm(20px);\n    }\n\n    .mainText {\n      @include mut.rem-padded(0, 20px, 0px, 20px);\n      color: c.$lighter-black-color;\n\n      @include mut.with-font($font-weight: 300, $font-size: 13px);\n      line-height: f.pxToEm(18px);\n    }\n  }\n}\n.interestingFactSlide:first-child{\n  @include mut.rem-margined($left:1px);\n}\n.slick-center .interestingFactSlide {\n  z-index: 1;\n}\n\n.interestingFactsSliderContainer .sliderClass {\n     height: 100%;\n}\n\n.interestingFactsSliderContainer .slick-list {\n  @include mut.rem-padded(130px);\n}\n\n.readMoreParagraph {\n  @include mut.rem-padded(0, 20px, 0px, 20px);\n  @include vnd.vendored(transition, '.5s ease');\n  cursor: pointer;\n\n  color: c.$dark-red-color;\n  @include mut.with-font(ft.$roboto-font, 300, 13px, normal);\n\n  &:hover {\n    color: c.$accented-red-color;\n  }\n}","@use '../variables/variables.fonts' as ft;\n@use '../variables/variables.colors' as c;\n@use '../mixins/vendor.mixins' as vnd;\n@use '../utils.functions' as f;\n\n// --------------------------Positioning--------------------------\n@mixin flexed(\n  $direction: row,\n  $align-items: stretch,\n  $justify-content: stretch,\n  $wrap: nowrap,\n  $gap: 0\n) {\n  display: flex;\n  flex-direction: $direction;\n  justify-content: $justify-content;\n  align-items: $align-items;\n  flex-wrap: $wrap;\n  gap: f.pxToRem($gap);\n}\n\n@mixin set-positioning($top, $right, $bottom, $left) {\n  @if ($top == 'auto' or unit($top) == '%') {\n    top: $top;\n  } @else {\n    top: f.pxToRem($top);\n  }\n  @if ($right == 'auto' or unit($right) == '%') {\n    right: $right;\n  } @else {\n    right: f.pxToRem($right);\n  }\n  @if ($bottom == 'auto' or unit($bottom) == '%') {\n    bottom: $bottom;\n  } @else {\n    bottom: f.pxToRem($bottom);\n  }\n  @if ($left == 'auto' or unit($left) == '%') {\n    left: $left;\n  } @else {\n    left: f.pxToRem($left);\n  }\n}\n\n@mixin positioned-as($position: relative, $top: auto, $right: auto, $bottom: auto, $left: auto) {\n  position: $position;\n  @include set-positioning($top, $right, $bottom, $left);\n}\n\n@mixin sized($width: auto, $height: auto) {\n  @if ($width == 'auto' or $width == 'inherit' or unit($width) == '%') {\n    width: $width !important;\n  } @else {\n    width: f.pxToRem($width) !important;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height !important;\n  } @else {\n    height: f.pxToRem($height) !important;\n  }\n}\n\n@mixin parent-sized {\n  @include sized(100%, 100%);\n}\n\n// --------------------------Centering----------------------------\n@mixin flex-centered($direction: row, $gap: 0) {\n  @include flexed($direction, center, center, nowrap, $gap);\n}\n\n@mixin absolute-centered($axes: 'both') {\n  position: absolute;\n\n  @if $axes == 'vertical' or $axes == 'v' {\n    top: 50%;\n    @include vnd.vendored(transform, translateY(-50%));\n  }\n  @else if $axes == 'horizontal' or $axes == 'h' {\n    left: 50%;\n    @include vnd.vendored(transform, translateX(-50%));\n  }\n  @else if $axes == 'both' or $axes == 'b' {\n    top: 50%;\n    left: 50%;\n    @include vnd.vendored(transform, translate(-50%, -50%));\n  }\n}\n\n// --------------------------Bordering----------------------------\n@mixin rounded(\n  $top-left: 0,\n  $top-right: 0,\n  $bottom-right: 0,\n  $bottom-left: 0,\n  $overflow: hidden\n) {\n  $rad: $top-left $top-right $bottom-right $bottom-left;\n\n  border-radius: $rad !important;\n  -webkit-border-radius: $rad !important;\n\n  overflow: $overflow !important;\n  background-clip: padding-box;\n}\n\n@mixin full-rounded($rad, $overflow: hidden) {\n  @include rounded($rad, $rad, $rad, $rad, $overflow);\n}\n\n@mixin circular($radius, $color: c.$pure-white-color) {\n  @include sized($radius, $radius);\n  @include full-rounded(50%);\n  background: $color;\n}\n\n// --------------------------Margins-----------------------------\n@mixin rem-margined($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  margin: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n\n@mixin rem-padded($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  padding: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n// --------------------------Appearance----------------------------\n@mixin backdrop-filtering-blur($f-size){\n  -webkit-backdrop-filter: blur($f-size);\n  -o-backdrop-filter: blur($f-size);\n  -moz-backdrop-filter: blur($f-size);\n\n  backdrop-filter: blur($f-size);\n}\n\n@mixin outlined($b-size: 2px, $color: red) {\n  outline: $b-size solid $color !important;\n}\n\n@mixin hidden {\n  display: none !important;\n  visibility: hidden !important;\n}\n\n@mixin visible($display: block) {\n  display: $display !important;\n  visibility: visible !important;\n}\n\n@mixin bg-image($file-path, $bg-size: cover) {\n  background-image: url($file-path);\n  background-repeat: no-repeat;\n  background-size: $bg-size;\n}\n\n@mixin colored($color: c.$pure-black-color, $bg-color: c.$pure-white-color) {\n  color: $color;\n  background-color: $bg-color;\n}\n// --------------------------Text----------------------------------\n@mixin truncated($line-num: 3, $text-overflow: ellipsis) {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: $text-overflow;\n\n  @supports (-webkit-line-clamp: $line-num) {\n    overflow: hidden;\n    text-overflow: $text-overflow;\n    white-space: initial;\n    display: -webkit-box;\n    -webkit-line-clamp: $line-num;\n    -webkit-box-orient: vertical;\n  }\n}\n\n// --------------------------Fonts---------------------------------\n@mixin with-font(\n  $font-family: ft.$roboto-font,\n  $font-weight: normal,\n  $font-size: 20px,\n  $font-style: normal\n) {\n  font-family: $font-family;\n  // px equivalent for browsers without rem support\n  font-size: $font-size;\n  font-size: f.pxToRem($font-size);\n  font-weight: $font-weight;\n  font-style: $font-style;\n}\n\n@mixin antialiased {\n  font-smoothing: antialiased;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n@mixin font-face(\n  $font-family,\n  $file-path,\n  $font-weight: normal,\n  $font-style: normal,\n  $fallback: sans-serif\n) {\n  $file-path: '@assets/fonts/#{$file-path}';\n\n  @font-face {\n    font-family: '#{$font-family}';\n\n    src: url($file-path + '.eot');\n    src: local($file-path),\n      url($file-path + '.eot?#iefix') format('embedded-opentype'),\n      url($file-path + '.woff2') format('woff2'),\n      url($file-path + '.woff') format('woff');\n\n    font-weight: $font-weight;\n    font-style: $font-style;\n    font-display: swap;\n  }\n\n  %#{$font-family} {\n    font: {\n      @if $fallback != '' {\n        family: '#{$font-family}', #{$fallback};\n      } @else {\n        family: '#{$font-family}';\n        weight: $font-weight;\n      }\n    }\n  }\n}\n// FadeIn FadeOut animations\n@mixin fadeKeyframes($type: \"fadeIn\", $name: \"fade\") {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n  @-moz-keyframes #{$name} {\n    @content;\n  }\n  @-ms-keyframes #{$name} {\n    @content;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","@use \"@assets/sass/_utils.functions.scss\" as f;\n\n/// Vendor prefixes.\n/// @var {list}\n$vendor-prefixes: (\n        '-moz-',\n        '-webkit-',\n        '-ms-',\n        ''\n);\n\n/// Properties that should be vendorized.\n/// @var {list}\n$vendor-properties: (\n        'align-content',\n        'align-items',\n        'align-self',\n        'animation',\n        'appearance',\n        'box-sizing',\n        'filter',\n        'flex',\n        'flex-basis',\n        'flex-direction',\n        'flex-flow',\n        'flex-grow',\n        'flex-shrink',\n        'flex-wrap',\n        'justify-content',\n        'order',\n        'pointer-events',\n        'transform',\n        'transition',\n        'transition-delay'\n);\n\n/// Values that should be vendorized.\n/// @var {list}\n$vendor-values: (\n        'flex',\n        'linear-gradient',\n        'radial-gradient',\n        'transform'\n);\n\n/// Vendorizes a declaration's property and/or value(s).\n/// @param {string} $property Property.\n/// @param {mixed} $value String/list of value(s).\n@mixin vendored($property, $value) {\n\n  // Determine if property should expand.\n  $expandProperty: index($vendor-properties, $property);\n\n  // Determine if value should expand (and if so, add '-prefix-' placeholder).\n  $expandValue: false;\n\n  @each $x in $value {\n    @each $y in $vendor-values {\n      @if $y == str-slice($x, 1, str-length($y)) {\n\n        $value: set-nth($value, index($value, $x), '-prefix-' + $x);\n        $expandValue: true;\n      }\n    }\n  }\n\n  // Expand property?\n  @if $expandProperty {\n    @each $vendor in $vendor-prefixes {\n      #{$vendor}#{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Expand just the value?\n  @else if $expandValue {\n    @each $vendor in $vendor-prefixes {\n      #{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Neither? Treat them as a normal declaration.\n  @else {\n    #{$property}: #{$value};\n  }\n}","$pure-white-color: #FFFFFF !important;\n$pure-black-color: #000000 !important;\n$lighter-black-color: #1D1F23 !important;\n\n$transparent-color: transparent !important;\n\n$accented-gray-color: #F4F4F4 !important;\n$lighter-gray-color: #D3CDCA !important;\n$darker-gray-color: #5D5D5D !important;\n$dark-gray-color: #4D4D4D !important;\n$darkish-gray-color: #A3A3A3 !important;\n\n$normal-gray-color: #7F7F7F !important;\n$normal-darker-gray-color: #7E7E7E !important;\n\n$dark-red-color: #8D1F16 !important;\n$accented-red-color: #E04031 !important;\n\n$slider-base-dot-color: #D9D9D9 !important;\n$timeline-reel-color: #D9D9D9 !important;\n$copyright-section-color: #C4C4C4 !important;\n\n$selected-arrow-color: #C4C4C4 !important;\n$base-arrow-color: #F2F2F2 !important;\n\n$base-arrow-filter-color: invert(97%) sepia(66%) saturate(423%)\n  hue-rotate(212deg) brightness(113%) contrast(90%) !important;\n$selected-arrow-filter-color: invert(87%) sepia(0%) saturate(479%)\n  hue-rotate(143deg) brightness(95%) contrast(81%) !important;\n$hovered-btn-filter-color: drop-shadow(0 2px 15px rgba(61, 0, 0, .25)) !important;\n\n$card-scrollbar-track-color: #DEDCDC !important;\n$modal-hover-color: #ECE9E9 !important;\n$burger-menu-color: #7F7F7F !important;\n$ant-menu-color: #E13F30 !important;","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$roboto-font: 'Roboto', 'Helvetica Neue', sans-serif !important;\n$closer-text-font: 'Closer Text', $roboto-font;"],"sourceRoot":""}]);
