t;\n  }\n  @if ($height == 'auto' or $height == 'inherit' or unit($height) == '%') {\n    height: $height !important;\n  } @else {\n    height: f.pxToRem($height) !important;\n  }\n}\n\n@mixin parent-sized {\n  @include sized(100%, 100%);\n}\n\n// --------------------------Centering----------------------------\n@mixin flex-centered($direction: row, $gap: 0) {\n  @include flexed($direction, center, center, nowrap, $gap);\n}\n\n@mixin absolute-centered($axes: 'both') {\n  position: absolute;\n\n  @if $axes == 'vertical' or $axes == 'v' {\n    top: 50%;\n    @include vnd.vendored(transform, translateY(-50%));\n  }\n  @else if $axes == 'horizontal' or $axes == 'h' {\n    left: 50%;\n    @include vnd.vendored(transform, translateX(-50%));\n  }\n  @else if $axes == 'both' or $axes == 'b' {\n    top: 50%;\n    left: 50%;\n    @include vnd.vendored(transform, translate(-50%, -50%));\n  }\n}\n\n// --------------------------Bordering----------------------------\n@mixin rounded(\n  $top-left: 0,\n  $top-right: 0,\n  $bottom-right: 0,\n  $bottom-left: 0,\n  $overflow: hidden\n) {\n  $rad: $top-left $top-right $bottom-right $bottom-left;\n\n  border-radius: $rad !important;\n  -webkit-border-radius: $rad !important;\n\n  overflow: $overflow !important;\n  background-clip: padding-box;\n}\n\n@mixin full-rounded($rad, $overflow: hidden) {\n  @include rounded($rad, $rad, $rad, $rad, $overflow);\n}\n\n@mixin circular($radius, $color: c.$pure-white-color) {\n  @include sized($radius, $radius);\n  @include full-rounded(50%);\n  background: $color;\n}\n\n// --------------------------Margins-----------------------------\n@mixin rem-margined($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  margin: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n\n@mixin rem-padded($top: 0, $right: 0, $bottom: 0, $left: 0) {\n  padding: f.pxToRem($top) f.pxToRem($right)\n          f.pxToRem($bottom) f.pxToRem($left) !important;\n}\n// --------------------------Appearance----------------------------\n@mixin backdrop-filtering-blur($f-size){\n  -webkit-backdrop-filter: blur($f-size);\n  -o-backdrop-filter: blur($f-size);\n  -moz-backdrop-filter: blur($f-size);\n\n  backdrop-filter: blur($f-size);\n}\n\n@mixin outlined($b-size: 2px, $color: red) {\n  outline: $b-size solid $color !important;\n}\n\n@mixin hidden {\n  display: none !important;\n  visibility: hidden !important;\n}\n\n@mixin visible($display: block) {\n  display: $display !important;\n  visibility: visible !important;\n}\n\n@mixin bg-image($file-path, $bg-size: cover) {\n  background-image: url($file-path);\n  background-repeat: no-repeat;\n  background-size: $bg-size;\n}\n\n@mixin colored($color: c.$pure-black-color, $bg-color: c.$pure-white-color) {\n  color: $color;\n  background-color: $bg-color;\n}\n// --------------------------Text----------------------------------\n@mixin truncated($line-num: 3, $text-overflow: ellipsis) {\n  overflow: hidden;\n  white-space: nowrap;\n  text-overflow: $text-overflow;\n\n  @supports (-webkit-line-clamp: $line-num) {\n    overflow: hidden;\n    text-overflow: $text-overflow;\n    white-space: initial;\n    display: -webkit-box;\n    -webkit-line-clamp: $line-num;\n    -webkit-box-orient: vertical;\n  }\n}\n\n// --------------------------Fonts---------------------------------\n@mixin with-font(\n  $font-family: ft.$roboto-font,\n  $font-weight: normal,\n  $font-size: 20px,\n  $font-style: normal\n) {\n  font-family: $font-family;\n  // px equivalent for browsers without rem support\n  font-size: $font-size;\n  font-size: f.pxToRem($font-size);\n  font-weight: $font-weight;\n  font-style: $font-style;\n}\n\n@mixin antialiased {\n  font-smoothing: antialiased;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n@mixin font-face(\n  $font-family,\n  $file-path,\n  $font-weight: normal,\n  $font-style: normal,\n  $fallback: sans-serif\n) {\n  $file-path: '@assets/fonts/#{$file-path}';\n\n  @font-face {\n    font-family: '#{$font-family}';\n\n    src: url($file-path + '.eot');\n    src: local($file-path),\n      url($file-path + '.eot?#iefix') format('embedded-opentype'),\n      url($file-path + '.woff2') format('woff2'),\n      url($file-path + '.woff') format('woff');\n\n    font-weight: $font-weight;\n    font-style: $font-style;\n    font-display: swap;\n  }\n\n  %#{$font-family} {\n    font: {\n      @if $fallback != '' {\n        family: '#{$font-family}', #{$fallback};\n      } @else {\n        family: '#{$font-family}';\n        weight: $font-weight;\n      }\n    }\n  }\n}\n// FadeIn FadeOut animations\n@mixin fadeKeyframes($type: \"fadeIn\", $name: \"fade\") {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n  @-moz-keyframes #{$name} {\n    @content;\n  }\n  @-ms-keyframes #{$name} {\n    @content;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","@use \"@assets/sass/mixins/_utils.mixins.scss\" as *;\n\n$closer-text-family: 'Closer Text';\n$roboto-family: 'Roboto';\n\n$closer-text-extra-light-family: 'Closer Text ExtLt';\n$closer-text-extra-bold-family: 'Closer Text ExtBd';\n$closer-text-semi-bold-family: 'Closer Text SemBd';\n$closer-text-medium-family: 'Closer Text Med';\n\n$closer-family:'Closer';\n$closer-light-family:'Closer-Light';\n$closer-medium-family:'Closer-Medium';\n\n//----------------------------------------Closer Text-----------------------------------------------\n\n@include font-face($closer-family, 'Closer-Light', 300, normal);\n@include font-face($closer-medium-family, 'Closer-Medium', normal, normal);\n\n@include font-face($closer-text-family, 'CloserText-Regular');\n@include font-face($closer-text-family, 'CloserText-Black', 900);\n@include font-face($closer-text-family, 'CloserText-BlackItalic', 900, italic);\n@include font-face($closer-text-family, 'CloserText-Bold', bold);\n@include font-face($closer-text-family, 'CloserText-BoldItalic', bold, italic);\n@include font-face($closer-text-family, 'CloserText-Light', 300);\n@include font-face($closer-text-family, 'CloserText-Italic', $font-style: italic);\n@include font-face($closer-text-family, 'CloserText-LightItalic', 300, italic);\n@include font-face($closer-text-family, 'CloserText-Thin', 100);\n@include font-face($closer-text-family, 'CloserText-ThinItalic', 100, italic);\n\n@include font-face($closer-text-extra-bold-family, 'CloserText-ExtraBold', bold);\n@include font-face($closer-text-extra-bold-family, 'CloserText-ExtraBoldItalic', bold, italic);\n\n@include font-face($closer-text-extra-light-family, 'CloserText-ExtraLight', 200);\n@include font-face($closer-text-extra-light-family, 'CloserText-ExtraLightItalic', 200, italic);\n\n@include font-face($closer-text-medium-family, 'CloserText-Medium', 500);\n@include font-face($closer-text-medium-family, 'CloserText-MediumItalic', 500, italic);\n\n@include font-face($closer-text-semi-bold-family, 'CloserText-SemiBold', 600);\n@include font-face($closer-text-semi-bold-family, 'CloserText-SemiBoldItalic', 600, italic);\n\n//----------------------------------------Roboto---------------------------------------------------\n@include font-face($roboto-family, 'Roboto-Regular');\n@include font-face($roboto-family, 'Roboto-Black', 900);\n@include font-face($roboto-family, 'Roboto-BlackItalic', 900, italic);\n@include font-face($roboto-family, 'Roboto-Bold', bold);\n@include font-face($roboto-family, 'Roboto-Light', 300);\n@include font-face($roboto-family, 'Roboto-Italic', $font-style: italic);\n@include font-face($roboto-family, 'Roboto-BoldItalic', bold, italic);\n@include font-face($roboto-family, 'Roboto-LightItalic', 300, italic);\n@include font-face($roboto-family, 'Roboto-Medium', 500);\n@include font-face($roboto-family, 'Roboto-Thin', 100);\n@include font-face($roboto-family, 'Roboto-ThinItalic', 100, italic);\n\n"],"sourceRoot":""}]);
