t-style: $font-style;\n}\n\n@mixin antialiased {\n  font-smoothing: antialiased;\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\n@mixin font-face(\n  $font-family,\n  $file-path,\n  $font-weight: normal,\n  $font-style: normal,\n  $fallback: sans-serif\n) {\n  $file-path: '@assets/fonts/#{$file-path}';\n\n  @font-face {\n    font-family: '#{$font-family}';\n\n    src: url($file-path + '.eot');\n    src: local($file-path),\n      url($file-path + '.eot?#iefix') format('embedded-opentype'),\n      url($file-path + '.woff2') format('woff2'),\n      url($file-path + '.woff') format('woff');\n\n    font-weight: $font-weight;\n    font-style: $font-style;\n    font-display: swap;\n  }\n\n  %#{$font-family} {\n    font: {\n      @if $fallback != '' {\n        family: '#{$font-family}', #{$fallback};\n      } @else {\n        family: '#{$font-family}';\n        weight: $font-weight;\n      }\n    }\n  }\n}\n// FadeIn FadeOut animations\n@mixin fadeKeyframes($type: \"fadeIn\", $name: \"fade\") {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n  @-moz-keyframes #{$name} {\n    @content;\n  }\n  @-ms-keyframes #{$name} {\n    @content;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","@use \"@assets/sass/_utils.functions.scss\" as *;\n@use \"@assets/sass/mixins/_utils.mixins.scss\" as mut;\n\n@mixin main-page-block-wrapper {\n  width: 100%;\n  @include mut.flex-centered();\n\n  .slick-dots {\n    @include mut.positioned-as($bottom: -58px, $left: 15px);\n  }\n\n  .slick-next:before,\n  .slick-prev:before {\n    position: relative;\n    top: pxToRem(-17px);\n  }\n\n  .slick-prev:before {\n    left: pxToRem(-73px);\n  }\n\n  .slick-next:before {\n    right: pxToRem(-81px);\n  }\n}","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$