Property: index($vendor-properties, $property);\n\n  // Determine if value should expand (and if so, add '-prefix-' placeholder).\n  $expandValue: false;\n\n  @each $x in $value {\n    @each $y in $vendor-values {\n      @if $y == str-slice($x, 1, str-length($y)) {\n\n        $value: set-nth($value, index($value, $x), '-prefix-' + $x);\n        $expandValue: true;\n      }\n    }\n  }\n\n  // Expand property?\n  @if $expandProperty {\n    @each $vendor in $vendor-prefixes {\n      #{$vendor}#{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Expand just the value?\n  @else if $expandValue {\n    @each $vendor in $vendor-prefixes {\n      #{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Neither? Treat them as a normal declaration.\n  @else {\n    #{$property}: #{$value};\n  }\n}","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$roboto-font: 'Roboto', 'Helvetica Neue', sans-serif !important;\n$closer-text-font: 'Closer Text', $roboto-font;"],"sourceRoot":""}]);
