n    font-weight: $font-weight;\n    font-style: $font-style;\n    font-display: swap;\n  }\n\n  %#{$font-family} {\n    font: {\n      @if $fallback != '' {\n        family: '#{$font-family}', #{$fallback};\n      } @else {\n        family: '#{$font-family}';\n        weight: $font-weight;\n      }\n    }\n  }\n}\n// FadeIn FadeOut animations\n@mixin fadeKeyframes($type: \"fadeIn\", $name: \"fade\") {\n  @-webkit-keyframes #{$name} {\n    @content;\n  }\n  @-moz-keyframes #{$name} {\n    @content;\n  }\n  @-ms-keyframes #{$name} {\n    @content;\n  }\n  @keyframes #{$name} {\n    @content;\n  }\n}","$code-tag-font: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;\n$body-tag-font: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',\n'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',\nsans-serif;\n\n$roboto-font: 'Roboto', 'Helvetica Neue', sans-serif !important;\n$closer-text-font: 'Closer Text', $roboto-font;","@use \"@assets/sass/_utils.functions.scss\" as f;\n\n/// Vendor prefixes.\n/// @var {list}\n$vendor-prefixes: (\n        '-moz-',\n        '-webkit-',\n        '-ms-',\n        ''\n);\n\n/// Properties that should be vendorized.\n/// @var {list}\n$vendor-properties: (\n        'align-content',\n        'align-items',\n        'align-self',\n        'animation',\n        'appearance',\n        'box-sizing',\n        'filter',\n        'flex',\n        'flex-basis',\n        'flex-direction',\n        'flex-flow',\n        'flex-grow',\n        'flex-shrink',\n        'flex-wrap',\n        'justify-content',\n        'order',\n        'pointer-events',\n        'transform',\n        'transition',\n        'transition-delay'\n);\n\n/// Values that should be vendorized.\n/// @var {list}\n$vendor-values: (\n        'flex',\n        'linear-gradient',\n        'radial-gradient',\n        'transform'\n);\n\n/// Vendorizes a declaration's property and/or value(s).\n/// @param {string} $property Property.\n/// @param {mixed} $value String/list of value(s).\n@mixin vendored($property, $value) {\n\n  // Determine if property should expand.\n  $expandProperty: index($vendor-properties, $property);\n\n  // Determine if value should expand (and if so, add '-prefix-' placeholder).\n  $expandValue: false;\n\n  @each $x in $value {\n    @each $y in $vendor-values {\n      @if $y == str-slice($x, 1, str-length($y)) {\n\n        $value: set-nth($value, index($value, $x), '-prefix-' + $x);\n        $expandValue: true;\n      }\n    }\n  }\n\n  // Expand property?\n  @if $expandProperty {\n    @each $vendor in $vendor-prefixes {\n      #{$vendor}#{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Expand just the value?\n  @else if $expandValue {\n    @each $vendor in $vendor-prefixes {\n      #{$property}: #{f.str-replace-all($value, '-prefix-', $vendor)};\n    }\n  }\n\n    // Neither? Treat them as a normal declaration.\n  @else {\n    #{$property}: #{$value};\n  }\n}"],"sourceRoot":""}]);
